
import { defineComponent, PropType, watch, ref } from 'vue'
import { IFormItem } from './type'

export default defineComponent({
  name: 'GForm',
  props: {
    modelValue: {
      type: Object,
      required: true
    },
    formItems: {
      type: Array as PropType<IFormItem[]>,
      default: () => []
    },
    //
    rowLayout: {
      type: Object,
      default: () => ({
        xs: 24, //  < 768px 
        sm: 24, // 768px <  < 992px 
        md: 12, // 992px <  < 1200px 
        lg: 8, // 1200px <  < 1920px 
        xl: 6 //  > 1920px 
      })
    },
    labelWidth: {
      type: String,
      default: '100px'
    },
    formItemStyle: {
      type: Object,
      default: () => ({ padding: '10px 5px;' })
    },
    hiddenItem: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    //props.modelVal,props
    const formData: any = ref({ ...props.modelValue })
    //formData,
    watch(
      formData,
      (newVal) => {
        emit('update:modelValue', newVal)
      },
      {
        deep: true,
        immediate: true
      }
    )

    return {
      formData
    }
  }
})
